import { BrowserRouter as Router, Route, Switch  } from 'react-router-dom'
import React, { Component } from 'react';
import './App.sass';

// SERVICES
import SlackBot from './SlackBot'

// COMPONENTS
import Emoji from './Emoji'
import Navigation  from './Navigation/Navigation'

// PAGES
import Home  from './Home/Home'
import Career  from './Career/Career'
import Projects  from './Projects/Projects'
import Roadmunk from './Roadmunk/Roadmunk'
import InvestOttawa from './InvestOttawa/InvestOttawa'
import IPService from './IPService';


const _404 = () => ( 
  <div>
    <h1>/404</h1>
    <h2><Emoji size='1.5em' name='shrug'/> This page doesn't exist</h2>
  </div>
  
)

class App extends Component {

  componentDidMount(){
    const ipData = IPService.getIpData()
    const gMapsLink = `https://www.google.com/maps/?q=${ipData.postal} ${ipData.city} ${ipData.country_name}`
    SlackBot.sendTextMessage(`🛰 Site visit from <${gMapsLink}|${ipData.city}, ${ipData.country_name}>`)
  }

  render() {
    return (
      <Router>
          <div>
              <Navigation />
              <div className="main-container">
                <div className='space-left' ></div>
                <div className='route-outlet'>
                  <Switch>
                    <Route exact path="/"     component={ Home } />
                    <Route exact path="/career"    component={ Career } />
                    <Route exact path="/projects"  component={ Projects } />

                    <Route exact path="/hello/roadmunk"  component={ Roadmunk } />
                    <Route exact path="/hello/InvestOttawa"  component={ InvestOttawa } />
                    

                    <Route component= { _404 } />
                  </Switch>
                </div>
                <div className='space-right'></div>
              </div>
          </div>
      </Router>
    );
  }
}

export default App;



