function sendPayloadToSlack(payload){
    
    if(window.location.hostname==='localhost') {
        console.log('Slack notification send')
        console.log(payload)
        return
    }
    const url = "https://hooks.slack.com/services/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";
    var xhr = new XMLHttpRequest();
    xhr.open('POST', url);
    xhr.onload = function() {
        if (xhr.status !== 200) {
            console.error("Slack Hook failed");
        }
    };
    xhr.send(JSON.stringify(payload));
}

function sendTextMessage(text) {
    sendPayloadToSlack({text})
}

export default class SlackBot {
    static sendPayloadToSlack = sendPayloadToSlack
    static sendTextMessage = sendTextMessage 

}




