function getIPData() {
    const request = new XMLHttpRequest();
    request.open("GET", "https://ipapi.co/json", false)
    request.send(null);

    if(request.status === 200)
        return JSON.parse(request.responseText)
    else return null

}

const IPService = {
    getIpData : () => {
        return getIPData()
    }
}

export default IPService


